import { Component, OnInit } from '@angular/core';
import { EChartsOption } from 'echarts';
import { DatacollectionService } from '../Datacollection-service.service'
import { AuthService } from "@auth0/auth0-angular";

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent implements OnInit {
  graph_line_chart: EChartsOption = {
    tooltip: {
      trigger: 'axis'
    },
    // legend: {
    //   data: ['sales', 'purchases'],
    //   textStyle: {
    //     color: '#9aa0ac'
    //   }
    // },
    toolbox: {
      show: !1
    },
    xAxis: [
      {
        type: 'category',
        data: ['2000', '2001', '2002', '2003', '2004', '2005'],
        axisLabel: {
          fontSize: 10,
          color: '#9aa0ac'
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisLabel: {
          fontSize: 10,
          color: '#9aa0ac'
        }
      }
    ],
    series: [
      {
        name: 'sales',
        type: 'bar',
        data: [0.668],
        // markPoint: {
        //   data: [
        //     {
        //       type: 'max',
        //       name: '???'
        //     },
        //     {
        //       type: 'min',
        //       name: '???'
        //     }
        //   ]
        // },
        markLine: {
          data: [
            {
              type: 'average'
            }
          ]
        }
      }

      // {
      //   name: 'purchases',
      //   type: 'bar',
      //   data: [35, 45, 47, 10, 35, 70],
      //   markLine: {
      //     data: [
      //       {
      //         type: 'average'
      //       }
      //     ]
      //   }
      // }
    ],
    color: ['#9f78ff', '#32cafe']
  };

  currentUser: any = {};
  public BiasData: any = null;
  attribute: any = null;
  breadscrums = [
    {
      title: 'Bias',
      items: ['Bias'],
      active: 'Bias Metrics'
    }
  ];
  constructor(
    private DatacollectionService: DatacollectionService,
    private authService: AuthService
  ) { }

  ngOnInit() {
    this.authService.user$.subscribe((user) => {
      this.currentUser = user;
      this.getData();
    })
  }
  
  private getData() {
    this.DatacollectionService.getBias(this.currentUser?.email)
      .subscribe((data) => {
        if (data) {
          console.log(data,"asa")
          this.BiasData = data;
        }
      })
    this.DatacollectionService.getAttribute(this.currentUser?.email)
      .subscribe(data => {
        if(data){
          this.attribute = data;
        }
      })
  };


}
