import { Component, OnInit } from '@angular/core';
import { DatacollectionService } from '../Datacollection-service.service'
import { EChartsOption } from 'echarts';
import { AuthService } from "@auth0/auth0-angular";

@Component({
  selector: 'app-Intersection',
  templateUrl: './Intersection.component.html',
  styleUrls: ['./Intersection.component.scss']
})

export class IntersectionComponent implements OnInit {
  attribute: any = null;
  overallEvalDropDown: [] = [];
  overallEvalData: [] = null;
  option3: any = null
  pridectiveData: any = [];
  currentUser: any = {};
  // public pridectiveData2: any = [];
  // public pridectiveDat22: any;


  graph_line_chart: EChartsOption = {
    tooltip: {
      trigger: 'axis'
    },
    // legend: {
    //   data: ['sales', 'purchases'],
    //   textStyle: {
    //     color: '#9aa0ac'
    //   }
    // },
    toolbox: {
      show: !1
    },
    xAxis: [
      {
        type: 'category',
        data: ['2000', '2001', '2002', '2003', '2004', '2005'],
        axisLabel: {
          fontSize: 10,
          color: '#9aa0ac'
        }
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisLabel: {
          fontSize: 10,
          color: '#9aa0ac'
        }
      }
    ],
    series: [
      {
        name: 'sales',
        type: 'bar',
        data: [0.668],
        // markPoint: {
        //   data: [
        //     {
        //       type: 'max',
        //       name: '???'
        //     },
        //     {
        //       type: 'min',
        //       name: '???'
        //     }
        //   ]
        // },
        markLine: {
          data: [
            {
              type: 'average'
            }
          ]
        }
      }

      // {
      //   name: 'purchases',
      //   type: 'bar',
      //   data: [35, 45, 47, 10, 35, 70],
      //   markLine: {
      //     data: [
      //       {
      //         type: 'average'
      //       }
      //     ]
      //   }
      // }
    ],
    color: ['#9f78ff', '#32cafe']
  };

  // public premitiveData: any = [];
  // public _premitiveData: any ;





  breadscrums = [
    {
      title: 'Bias',
      items: ['Bias'],
      active: 'Feature-wise Analytics'
    }
  ];
  // pridectiveDropdown2: any;

  constructor(
    private DatacollectionService: DatacollectionService,
    private authService: AuthService
  ) { }

  ngOnInit() {
    this.authService.user$.subscribe((user) => {
      this.currentUser = user;
      this.getDropDown()
      this.getDropDown2()
      this.getDropDown3()
    })
  }

  private getDropDown3() {
    this.DatacollectionService.getProductedList2(this.currentUser?.email)
      .subscribe(data => {
        console.log(data)
      })
  }

  private getDropDown2() {
    this.DatacollectionService.getProductedDataList(this.currentUser?.email)
      .subscribe(data => {
        if (data?.data) {
          console.log(data?.data)
          this.pridectiveData = data?.data;
        }
      })
  };

  private getDropDown() {
    this.DatacollectionService.getOverAllEvalutionDropDown(this.currentUser?.email)
      .subscribe(data => {
        if (data?.data) {
          console.log(data?.data)
          this.overallEvalDropDown = data?.data;
        }
      })
    this.DatacollectionService.getAttribute(this.currentUser?.email)
    .subscribe(data => {
      if(data){
        this.attribute = data;
      }
    })
  }

  // used in html
  onChange(data: any) {
    return
  }

  overallEval() {
    this.overallEvalData = [];
    this.DatacollectionService.getOverAllEvalutionData(this.option3, this.currentUser?.email)
      .subscribe(data => {
        if (data) {
          console.log(data, "overallEvalData")
          this.overallEvalData = data;
        }
      })
  }
  // not used 
  // overallEval2() {
  //   this.overallEvalData = [];
  //   this.DatacollectionService.getIntersectional(this.premitiveData, this.pridectiveData2)
  //     .subscribe(data => {
  //       this.overallEvalData = data;
  //     })
  // }
}
