import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { BiasRoutingModule } from './bias-routing.module';
import { MainComponent } from './main/main.component';
import {  mainDashboardRes} from './mainDashboardRes/mainDashboardRes.component';

import { Dashboard2Component } from './dashboard2/dashboard2.component';
import { Dashboard3Component } from './dashboard3/dashboard3.component';
import { IntersectionComponent } from './Intersection/Intersection.component';

import { ChartsModule as chartjsModule } from 'ng2-charts';
import { NgxEchartsModule } from 'ngx-echarts';
import { GaugeModule } from 'angular-gauge';
import { MatIconModule } from '@angular/material/icon';
import { MatMenuModule } from '@angular/material/menu';
import { MatButtonModule } from '@angular/material/button';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { NgApexchartsModule } from 'ng-apexcharts';
import { ComponentsModule } from '../shared/components/components.module';
import { SharedModule } from './../shared/shared.module';

import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { MatTooltipModule } from '@angular/material/tooltip'

import { MatSelectModule } from '@angular/material/select';
import { SummaryComponent } from './summary/summary.component';

@NgModule({
  declarations: [mainDashboardRes,IntersectionComponent,MainComponent, Dashboard2Component, Dashboard3Component, SummaryComponent],
  imports: [
    CommonModule,
    MatTooltipModule,
    MatFormFieldModule,
    MatSelectModule,
    MatInputModule,
    BiasRoutingModule,
    chartjsModule,
    MatMenuModule,
    MatIconModule,
    MatButtonModule,
    PerfectScrollbarModule,
    NgApexchartsModule,
    NgxEchartsModule.forRoot({
      echarts: () => import('echarts')
    }),
    GaugeModule.forRoot(),
    ComponentsModule,
    SharedModule
  ]
})
export class BiasModule {}
